.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
}

.container {
    margin-top: var(--header-height);
    max-width: 100%;
    width: 100%;
    display: flex;
    padding: 0 100px;
    flex: 1; 
}
.content {
    flex: 1;
    width: 100%;
}
.footer {
    width: 100%;
}

/* mobile */
@media only screen and (max-width: 767px) {
    .container {
        padding: 0 20px;
    }
}

