.wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #1d1d1f;
    margin-top: 40px;
    padding: 20px 0;
    background-color: #f5f5f7;
    border-radius: 13px;
}

.title {
    font-size: 3rem;
    margin-bottom: 24px;
    text-align: center;
}

.list {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 7px;
    padding: 0 20px;
}

.loading {
    width: 100%;
    text-align: center;
    font-size: 1.8rem;
    color: #666;
    padding: 20px;
}

.error {
    width: 100%;
    text-align: center;
    font-size: 1.8rem;
    color: #ff3b30;
    padding: 20px;
}

/* mobile */
@media only screen and (max-width: 767px) {
    .list {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 0;
    }
}

/* tablet */
@media only screen and (min-width: 768px) and (max-width: 1024px) {
    .list {
        grid-template-columns: repeat(3, 1fr);
    }
}
